.not-found,
.not-found-data {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
}
.not-found-data
{
  height: calc(100% - 45px);

}
.not-found h1 {
  font-size: 15rem;
  margin-bottom: 0;
}

.not-found-data h1 {
  font-size: 10rem;
  margin-bottom: 0;
}

.not-found p,
.not-found-data p {
  font-size: 3rem;
  margin-top: 0.5rem;
  font-weight: bold;
}
