.div-datatable {
  width: 100%;
  height: 100%;
  box-shadow: var(--Box-Shadow1);
  overflow: hidden;
}
.div-datatable .table-footer,
.div-datatable .table-body {
  width: 100%;
}
.div-datatable .table-body {
  flex-grow: 1;
  overflow: auto;
}

.table-body table {
  width: 100%;
  border-collapse: collapse;
  border-radius: var(--Small-Round-Border);
}
.table-body table img {
  max-height: calc(var(--Extra-Large-Padding) * 2.5);
  max-width: calc(var(--Extra-Large-Padding) * 3.5);
  border-radius: var(--Small-Round-Border);
}
.table-body table thead th {
  position: sticky;
  border-bottom: var(--Global-Border);
  background-color: var(--Alternative-Background-Color);
  text-align: left;
  top: 0;
  z-index: 2;
  font-weight: 500;
  font-size: 1.1rem;
}
.table-body table thead th,
.table-body table td {
  padding: calc(var(--Small-Padding) * 1.2 ) var(--Small-Padding);
  max-width: 25vw;
}
.table-body table tr:nth-child(even),
.table-body table tr:hover {
  background-color: var(--Alternative-Background-Color);
}
.max-lines-2 {
    display: -webkit-box;
    -webkit-box-orient: vertical;
    line-height: 1.5; 
    -webkit-line-clamp: 2;
    overflow: hidden;
    text-overflow: ellipsis;
    /* Ensure text wraps within the element */
    white-space: normal;
    /* Set a fixed height to show half of the third line */
    max-height: calc(2.5 * 1.2em); /* 1.2em is line height, adjust as needed */
  }
  .div-datatable .table-footer
  {
    padding: var(--Small-Padding);
  }
  .div-datatable .table-footer .pag-btn
  {
    justify-content: right;
    padding-right: var(--Small-Padding);
  }
  .div-datatable .table-footer button
  {
    margin: calc(var(--Small-Padding) / 2);
    padding: calc(var(--Small-Padding) / 2);
    min-height: calc(var(--Extra-Large-Padding) * 1.3);
    min-width: calc(var(--Extra-Large-Padding) * 1.3);
    border-radius: var(--Small-Round-Border);
    box-shadow: var(--Box-Shadow3);
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .div-datatable .table-body tbody tr td .action-button
  {
    padding: 0px 0.3rem;
    margin-left: 0.1rem;
    background-color: transparent;
    min-height: calc(var(--Extra-Large-Padding) * 1.5);
    z-index: 0;
  }
  .div-datatable .table-body tbody tr td .action-button:hover
{
    background-color: var(--Primary-Background-Color);
    box-shadow: var(--Box-Shadow3);
    cursor: pointer;
}
  .div-datatable .table-body tbody tr td .action-button .icon
  {
    color: var(--Primary-Color3);
  }
